.disabled {
  color: $text-muted;
  cursor: $cursor-disabled;
  user-select: none;

  > select,
  > input[type="checkbox"],
  > input[type="radio"],
  > input[type="file"],
  > .form-control,
  > .input-group-btn .btn {
    pointer-events: none;
    user-select: none;
  }

  > .form-control,
  > .custom-select,
  > .input-group-addon {
    color: $text-muted;
    background-color: $input-bg-disabled;
  }
  > .input-group-addon .fa-spin {
    animation: none;
  }
  > .input-group-btn .btn {
    @include opacity(.65);
  }
  &.input-group-select .input-group-addon {
    background-color: transparent;
  }
  &.input-group-select-nofill {
    border-bottom: 1px solid $input-border;
    > .custom-select {
      background-color: transparent;
    }
  }
}

.form-control[disabled] {
  + .input-group-addon {
    color: $text-muted;
    background-color: $input-bg-disabled;
    cursor: not-allowed;
  }
}

.form-control-feedback {
  z-index: 3;
}

// feedback states
.has-success {
  .form-control {
    border-color: $state-success-border;
    &:focus {
      border-color: darken($state-success-border, 10%);
    }
  }
  .input-group-addon {
    border-color: $state-success-border;
    background-color: inherit;
  }
  > .focusin,
  &.focusin {
    .input-group-addon {
      border-color: darken($state-success-border, 10%);
    }
  }
  .btn-clearable {
    color: $state-success-text;
  }
  .popover,
  .popover-title {
    color: #fff;
    background-color: $brand-success;
    border-color: darken($brand-success, 10%);
  }
  .popover {
    &.top > .arrow {
      border-top-color: darken($brand-success, 5%);
      &:after {
        border-top-color: $brand-success;
      }
    }
    &.right > .arrow {
      border-right-color: darken($brand-success, 5%);
      &:after {
        border-right-color: $brand-success;
      }
    }
    &.bottom > .arrow {
      border-bottom-color: darken($brand-success, 5%);
      &:after {
        border-bottom-color: $brand-success;
      }
    }
    &.left > .arrow {
      border-left-color: darken($brand-success, 5%);
      &:after {
        border-left-color: $brand-success;
      }
    }
  }

  .tooltip-inner {
    background-color: $brand-success;
  }
  .tooltip {
    &.top .tooltip-arrow,
    &.top-left .tooltip-arrow,
    &.top-right .tooltip-arrow {
      border-top-color: $brand-success;
    }
    &.right .tooltip-arrow {
      border-right-color: $brand-success;
    }
    &.left .tooltip-arrow {
      border-left-color: $brand-success;
    }
    &.bottom .tooltip-arrow,
    &.bottom-left .tooltip-arrow,
    &.bottom-right .tooltip-arrow {
      border-bottom-color: $brand-success;
    }
  }
}
