// remove shadow from feedback states
.has-success,
.has-warning,
.has-error {
  .form-control {
    box-shadow: none;
    &:focus {
      box-shadow: none;
    }
  }
  .input-group-select-nofill > .custom-select {
    border-width: 0 0 1px;
    border-style: dashed;
    border-radius: 0;
  }
}