.avatar {
  width: 46px;
  height: auto;
  border-radius: 62px;

  &-mini {
    width: 24px;
  }
  &-xs {
    width: 30px;
  }
  &-sm {
    width: 36px;
  }
  &-md {
    width: 58px;
  }
  &-lg {
    width: 80px;
  }
  &-xl {
    width: 122px;
  }
}

.avatar-square {
  border-radius: 0;
}

.user-avatar {
  padding: 6px 0 4px;
  color: $text-color;
  @include clearfix;

  a {
    color: inherit;

    @include hover-focus {
      color: inherit;
      text-decoration: none;
    }
  }

  .avatar {
    position: relative;
    margin-right: 8px;
    float: left;
    z-index: 1;
  }
  .avatar-badge {
    position: absolute;
    bottom: 0;
    right: 4px;
    display: block;
    padding: 1px;
    width: 16px;
    height: 16px;
    text-align: center;
    line-height: 1;
    font-size: 10px;
    color: #fff;
    background-color: $gray;
    border-radius: 9px;
    border: 2px solid #fff;
    z-index: 2;

    &.online {
      background-color: $brand-success;
    }
    &.idle {
      background-color: $brand-warning;
    }
    &.busy {
      background-color: $brand-danger;
    }
    &.offline {
      color: $gray-light;
      background-color: $gray-light;
    }
  }
  .avatar-wrapper {
    position: relative;
    display: inline-block;
    float: left;
    .avatar {
      float: none;
    }

    &.has-hoverable-badge {
      @include hover-focus {
        .avatar-badge > .fa {
          &:before {
            content: "\f0d7";
          }
        }
        .offline {
          color: #fff;
        }
      }
    }
  }
  .avatar-title,
  .avatar-hint {
    margin-top: 0;
    margin-bottom: 0;
  }
  .avatar-hint {
    color: $text-muted;
  }
  .btn {
    color: $text-muted;
    outline: 0 !important;

    @include hover-focus {
      color: inherit;
    }
  }

  @include hover-focus {
    .avatar-hint,
    .btn {
      color: inherit;
    }
  }
}

.user-avatar-inverse {
  color: $gray-lighter;
}

a.avatar,
a.user-avatar {
  @include hover-focus {
    text-decoration: none;
  }
}

.avatar-boot {
  border: 1px solid;
  padding: 4px;
}


// Grouping
.avatar-group {
  display: inline-block;

  .avatar-wrapper + .avatar-wrapper {
    display: inline-block;
    margin-left: -($line-height-computed);
  }
}