.timeline {
  position: relative;
  padding: $line-height-computed 0;
  margin-bottom: $line-height-computed;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 18px;
    height: 100%;
    width: 4px;
    background-color: darken($border-color, 2%);
    @media only screen and (min-width: $screen-md-max) {
      left: 50%;
      margin-left: -2px;
    }
  }
}

.timeline-block {
  position: relative;
  @include clearfix;

  @media only screen and (min-width: $screen-md-max) {
    &:nth-child(even) {
      .timeline-panel {
        float: right;
        &:before,
        &:after {
          left: auto;
          right: 100%;
        }
        &:before {
          border-right: 10px solid $panel-default-border;
          border-left: 0;
        }
        &:after {
          border-right: 8px solid #fff;
          border-left: 0;
        }
      }

      .timeline-date {
        left: auto;
        right: 123%;
        text-align: right;
      }
    }
  }
}

.timeline-badge {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-shadow: 0 0 0 5px white;
  /* Force Hardware Acceleration in WebKit */
  transform: translateZ(0);
  backface-visibility: hidden;
  @media only screen and (min-width: $screen-md-max) {
    left: 50%;
    margin-left: -20px;
  }
}

.timeline-icon {
  display: block;
  width: 36px;
  height: 36px;
  position: relative;
  left: 50%;
  top: 50%;
  margin-left: -18px;
  margin-top: -18px;
}

.timeline-panel {
  position: relative;
  margin-left: 60px;

  &:after,
  &:before {
    content: '';
    position: absolute;
    top: 12px;
    left: auto;
    right: 100%;
    height: 0;
    width: 0;
    border: 8px solid transparent;
  }
  &:before {
    border-right: 10px solid $panel-default-border;
  }
  &:after {
    z-index: 1;
    border-right: 8px solid #fff;
  }

  @media only screen and (min-width: $screen-md-max) {
    margin-left: 0;
    width: 45%;

    &:before,
    &:after {
      left: 100%;
      right: auto;
      border-right: 0;
    }
    &:before {
      border-left: 10px solid $panel-default-border;
    }
    &:after {
      border-left: 8px solid #fff;
    }
  }
}

.timeline-date {
  position: absolute;
  left: 123%;
  top: 8px;
  color: $text-muted;
  width: 50%;
}